import React from 'react';
import {Link} from 'react-router-dom';
import 'regenerator-runtime/runtime';
import Spinner from '../../widgets/Spinner';
import {goFetch, cors, getToken, setToken} from "../../../api/misc";

class Order extends React.Component {

  constructor(props) {
    super(props);
    const symbol = props.location.pathname.replace('/d/Order/', '') || '000002';
    const token = getToken();
    this.state = {
      orders: null,
      token,
      auth: token ? null : false,// null: 还未鉴权; false: 鉴权失败
      symbol
    }
  }

  async componentDidMount() {
    const {symbol, token, auth} = this.state;
    if (auth === false) {
      return;
    }
    const res = await goFetch('/orders?symbol=' + symbol, {
      headers: {'user-token': token}
    });
    if (res.status === 401) {// 鉴权失败
      setToken('');
      this.setState({
        token: '',
        auth: false
      });
      return;
    }
    const json = await res.json() || [];

    const {data: stock} = await (await goFetch('/stock/' + symbol)).json();
    const {data: live} = await this.pankou(stock);
    live.clz = live.change[0] === '-' ? 'is-negative' : 'is-positive';
    const data = json.reduce((p, v) => {
      p.orders.push({...v, date: v.date.split('T')[0]});
      const {unit_price} = v;
      let {min = unit_price, max = unit_price, count = 0, quantity = 0, total_price = 0, total_fee = 0} = p[v.type];
      min = Math.min(min, unit_price);
      max = Math.max(max, unit_price);
      count += 1;
      p[v.type] = {
        min,
        max,
        count,
        quantity: quantity + v.quantity,
        total_price: total_price + unit_price * v.quantity,
        total_fee: total_fee + v.total_fee,// 包含税费的成本
      };

      return p;
    }, {
      orders: [],
      BUY: {total_fee: 0},
      SELL: {total_fee: 0},
      DIVIDEND_TAX: {total_fee: 0},
      DIVIDEND: {total_fee: 0},
      auth: true,
      stock,
      live
    });


    this.setState(data);
    setToken(token);
  }

  async pankou({symbol, market}) {
    // 盘口数据
    const res = await goFetch(`/stock/${symbol}/live`);
    return await res.json();
    // const res = await cors(`https://hq.sinajs.cn/list=${market.toLowerCase()}${symbol}`);
    // const t = await res.text();
    // // 2 昨日收盘数据，3 当前数据
    // const {2: open, 3: current} = t.split(',');
    // return {
    //   data: {
    //     current,
    //     change: (current - open).toFixed(2),
    //     percentage: ((current - open) / open * 100).toFixed(2)
    //   }
    // }
  }

  buildContent() {
    const {auth, orders} = this.state;

    if (auth === null) {
      return <div>
        加载中...
        <Spinner/>
      </div>
    }
    if (auth === false) {
      return <div>
        您需要登录才能访问此页面.
        <Link to='/d/OrderAgg'>返回登录页</Link>
      </div>
    }
    if (!orders || !orders.length) {
      return '没有订购记录';
    }
    return <table className='table is-striped' style={{width: '100%'}}>
      {thead}
      <tbody>
      {
        orders.map((o, i) => {
          return <tr key={o.id}>
            <td>{i + 1}</td>
            <td>{o.date}</td>
            <td>{o.securities}</td>
            <td className={o.type === 'SELL' ? 'sell' : null}>{o.type}</td>
            <td>{o.quantity}</td>
            <td>{o.unit_price}</td>
            <td>{o.charge}</td>
            <td>{o.tax}</td>
            <td>{o.transfer_fee}</td>
            <td>{o.total_fee}</td>
          </tr>
        })
      }
      </tbody>
    </table>
  }

  render() {
    const {orders, BUY: buy = {}, SELL: sell = {}, stock, live = {}, DIVIDEND: dividend = {}, DIVIDEND_TAX: dividend_tax = {}} = this.state;

    const total_cost = buy.total_fee - sell.total_fee - dividend.total_fee + dividend_tax.total_fee;
    const market_value = live.current * (buy.quantity - (sell.quantity || 0));
    const profit = market_value - total_cost;
    const quantity_held = buy.quantity - (sell.quantity || 0);
    return <div className='container stock'>
      {orders ? (<article className="message is-dark">
        <div className="message-header">
          <p>{stock.name}(<Link to={'/d/Stock/' + stock.symbol}>{stock.symbol}</Link>)</p>
          <Link to='/d/OrderAgg'>返回</Link>
        </div>
        <div className="message-body">
          <div className="columns">

            <div className="column">
              <dl>
                <li>当前价: <span className={live.clz}>{live.current}</span></li>
                <li>涨跌额: <span className={live.clz}>{live.change}</span></li>
                <li>涨跌幅: <span className={live.clz}>{live.percentage}%</span></li>
                <li>52周最低: <span>{live.low52week}</span></li>
                <li>52周最高: <span>{live.high52week}</span></li>
                <li>低涨: <span>{((live.current - live.low52week) / live.low52week * 100).toFixed(2)}%</span></li>
                <li>高差: <span>{((live.high52week - live.current) / live.current * 100).toFixed(2)}%</span></li>
              </dl>
            </div>

            <div className="column">
              <dl>
                <li>买入次数: {buy.count}</li>
                <li>买入最大单价: {buy.max}</li>
                <li>买入最小单价: {buy.min}</li>
                <li>买入平均单价: {(buy.total_price / buy.quantity).toFixed(2)}</li>
                <li>买入平均成本: {(buy.total_fee / buy.quantity).toFixed(2)}</li>
                <li>当前持仓成本: {total_cost.toFixed(2)}</li>
                <li>当前持仓市值: {market_value.toFixed(2)}</li>
              </dl>
            </div>
            <div className="column">
              <dl>
                <li>卖出次数: {sell.count}</li>
                <li>卖出最大单价: {sell.max}</li>
                <li>卖出最小单价: {sell.min}</li>
                <li>卖出平均单价: {sell.quantity && (sell.total_price / sell.quantity).toFixed(2)}</li>
                <li>卖出平均成本: {sell.quantity && (sell.total_fee / sell.quantity).toFixed(2)}</li>
                <li>当前盈亏: <span className={profit < 0 ? 'is-negative' : 'is-positive'}>{profit.toFixed(2)}</span></li>
                <li>当前盈亏率: <span
                  className={profit < 0 ? 'is-negative' : 'is-positive'}>{quantity_held > 0 ? ((profit / total_cost) * 100).toFixed(2) + '%' : '-'}</span>
                </li>
              </dl>
            </div>
          </div>
        </div>
      </article>) : void 0}
      {this.buildContent()}
    </div>
  }
}

const thead = (<thead>
<tr>
  <th>序号</th>
  <th>日期</th>
  <th>券商</th>
  <th>类型</th>
  <th>数量</th>
  <th>单价</th>
  <th>佣金</th>
  <th>税费</th>
  <th>其它</th>
  <th>总费用</th>
</tr>
</thead>);

export default Order
